.myChart, .gold, .emerald{
    background: none;
}

.orgchart-container {    
    background-color: white;
    height: 100%;
    margin: 0;
    border: 0;
    border-radius: 0;
}

.oc-content{
    overflow: hidden;
}
/*SILVER*/
.myChart ul li .oc-node .oc-heading{
    background-color: #bbbfbf !important;
}

.orgchart.myChart > ul > li > ul li::before {
    border-top-color: #bbbfbf;
}

.orgchart ul li .oc-node .oc-content{
    border: 1px solid #bbbfbf;
}

.orgchart.myChart > ul > li > ul li .oc-node::before,
.orgchart.myChart ul li .oc-node:not(:only-child)::after {
    background-color: #bbbfbf;
}

.orgchart.myChart ul li .oc-node:hover{
    background-color: #bbbfbf;
}

.orgchart.myChart ul li .oc-node.selected{
    background-color: #bbbfbf;
}

